,154,59,0,0,0,0]},"type":"pure"},{"kind":"Input","type":"object","index":6,"value":"0xaaf735bf83ff564e1b219a0d644de894ef5bdc4b2250b126b2a46dd002331821"},{"kind":"Input","type":"object","index":7,"value":"0xf87a8acb8b81d14307894d12595541a73f19933f88e1326d5be349c7a6f7559c"}]}]}';

